export default {
	data() {
		return {
			name: '赵鸿飞',
			data: [],
			search: '', //检索内容，临时为空
		}
	},
	mounted() {
		uni.request({
			url: this.$store.state.url + 'WO/GetWOsFE',
			method: 'GET',
			data: {
				pageNum: '1',
				numPerPage: '',
				orderField: '',
				orderDirection: '',
				search: this.search
			},
			header: {
				'Token': this.$store.state.token
			},
			success: (res) => {
				this.data = JSON.parse(res.data).Data.Dtos
				console.log(this.data)
			}
		});
	},
	methods: {
		asdurl(item) {

			this.$store.state.codeurlid = item
			if (item.Status == 6) {
				this.$store.state.codeurlids = 1
			}
			if (item.Status == 7 || item.Status == 25) {
			  	this.$store.state.codeurlids = 2
			}
			if (item.Status == 8 || item.Status == 26) {
			 	this.$store.state.codeurlids = 3
			 }
			uni.navigateTo({
				url: '/pages/home/works/one/one'
			});


		}
	}
}
